<template>
    <form @submit.prevent="handleSubmit">
        <label for="">Email:</label>
        <input type="email" required v-model="email">
        <label for="">Password:</label>
        <input type="password" required v-model="password">
        <div v-if="passwordError" class="error">{{ passwordError }}</div>
        <label>Role:</label>
        <select v-model="role">
            <option value="developer">Web Developer</option>
            <option value="designer">Web Designer</option>
        </select>
        <label>Skills:</label>
        <input type="text" v-model="tempSkill">
        <button @click="addSkill">Add Skill</button>
        <div class="skills" v-for="skill in skills" :key="skill">
            <input type="checkbox" checked @click="deleteSkill(skill)">
            <label>{{ skill }}</label>
        </div>
        <div class="terms">
            <input type="checkbox" v-model="terms" required>
            <label>ACCEPT TERMS AND CONDITIONS</label>
        </div>
        <div class="submit">
            <button>Create An Account</button>
        </div>

    </form>

</template>

<script>

export default {
    data() {
        return {
            email: "",
            password: "",
            role: "",
            terms: false,
            tempSkill: "",
            skills: [],
            passwordError: ""
        }
    }, methods: {
        addSkill(e) {
            // if (e.key === "Enter" && this.tempSkill) {
            //     if (!this.skills.includes(this.tempSkill)) {
            //         this.skills.push(this.tempSkill)

            //         console.log(this.skills)
            //     }
            //     this.tempSkill = ""
            // }
            if (!this.skills.includes(this.tempSkill) && this.tempSkill) {
                this.skills.push(this.tempSkill)
                this.tempSkill = ""
            }

        }, deleteSkill(skill) {
            // this.skills = this.skills.filter(item => item !==skill)
            let index = this.skills.indexOf(skill)
            this.skills.splice(index, 1)
            console.log(this.skills)
        }, handleSubmit() {
            this.passwordError = this.password.length > 5 ? "" : "Password must be at least 6 characters long"
            if (!this.passwordError) {

                console.log("Email: ", this.email)
                console.log("Skills: ", this.skills)
                console.log("Role: ", this.role)
            }

        }
    }

}
</script>

<style>
form {
    max-width: 420px;
    margin: 30px auto;
    background: white;
    text-align: left;
    padding: 40px;
    border-radius: 10px;
}

label {
    color: rgb(31, 27, 27)k;
    display: inline-block;
    margin: 25px 0 15px;
    font-size: 0.6em;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
}

input,
select {
    display: block;
    padding: 10px 6px;
    width: 100%;
    border: none;
    border-bottom: 1px solid #ddd;
    color: #555;
}

input[type="checkbox"] {
    display: inline-block;
    width: 16px;
    margin: 0 10px 0 0;
    position: relative;
    top: 2px;
    accent-color: #0b6dff;

}

button {
    background: #0b6dff;
    border: 0;
    padding: 10px 20px;
    margin-top: 20px;
    color: white;
    border-radius: 20px;
}

.submit {
    text-align: center;
}

.error {
    color: #ff0062;
    margin-top: 10px;
    font-size: 0.8em;
    font-weight: bold;
}
</style>